import * as types from '../mutation-types';

const state = {
    life: {
        Power: false,
        Water: false,
        Gas: false,
        Heat: false,
        Television: false
    },
    power: {
        item: '',
        area: '',
        company: '',
        companyName: '',
        no: '',
        name: '',
        money: ''
    },
    historyList: [
        {
            id: '01',
            area: "03",
            companyName: '3',
            company: "033",
            href: "LifePower",
            item: "",
            name: "",
            mark: '',
            no: "5654654"
        }, {
            id: '02',
            area: "03",
            company: "032",
            companyName: '2',
            href: "LifeHeat",
            item: "",
            name: "",
            mark: '',
            no: "3456754"
        }] // historyList
};

// getters
const getters = {
    history: state => state.life,   // 
    historyList: state => state.historyList
};

// actions
const actions = {
    showHistory: ({ commit }, data) => {
        commit('HISTORY', data);
    },
    setHistory: ({ commit }, data) => {
        commit('SET_HISTORY', data);
    },
    //  
    keepPowerInfo: ({ commit }, data) => {
        commit('KEEP_POWER_INFO', data);
    },
    //  
    clearPowerInfo: ({ commit }) => {
        commit('CLEAR_POWER_INFO');
    }
};

// mutations
const mutations = {
    HISTORY(state, data) {
        state.historyList.push(data);
    },
    SET_HISTORY(state, data) {
        state.life[data.item] = data.status;
    },
    KEEP_POWER_INFO(state, data) {
        state.power = Object.assign(state.power, data);
    },
    CLEAR_POWER_INFO(state) {
        state.power = {
            item: '',
            area: '',
            company: '',
            companyName: '',
            no: '',
            name: '',
            money: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



