import Mock from 'mockjs';

Mock.mock('/mock/bank/list',
    {"hotlist|10-20": [
        {
            name: /[]{1}/,
            cnName: /[A-Z]{10}/,
            isHot: true
        }
    ],
    "list|600-700": [
        {
            name: /[]{1}/,
            cnName: /[A-Z]{10}/,
            isHot: false
        }
    ]}
);



