import Mock from 'mockjs';

Mock.mock('/mock/payee/list',
    {"hotlist|10-30": [
        {
            id: '@id',
            name: /[][]{1,3}/,
            cnName: /[A-Z]{10}/,
            bankName: /[A-Z]{5}/,
            bankCity: '@city',
            bankBranch: '@city',
            bankNo: /\d{15}/,
            account: /6226\d{12}/,
            phone: /1\d{10}/,
            isLately: true,
            "isTop|1": true
        }
    ],
    "list|100-200": [
        {
            id: '@id',
            name: /[][]{1,3}/,
            cnName: /[A-Z]{10}/,
            bankName: /[A-Z]{5}/,
            bankCity: '@city',
            bankBranch: '@city',
            bankNo: /\d{15}/,
            account: /6226\d{12}/,
            phone: /1\d{10}/,
            isLately: false,
            "isTop|1": true,
            'tranType|1': ['', '', '', '']
        }
    ]}
);



